import React, { useEffect, useState } from 'react'
import { useForm } from "react-hook-form";
import NewApi from '../../../api/NewApi';
import { useHistory, useParams } from "react-router-dom";


const UpdateNew = () => {

    let history = useHistory();
    let { id } = useParams()
    const [news, setNews] = useState({})
    const { register, handleSubmit, reset, watch, formState: { errors } } = useForm();
    const onSubmit = (data) => {
        var body = {
            _id: id,
            title: data.title,
            content: data.content,
            image: data.image
        }
        const CallApi = async () => {
            try {
                const { data } = await NewApi.update(id, body)
                console.log(data);
                alert("Cp nht tin tc thnh cng");
                history.push("/admin/news");

            } catch (error) {
                alert("Cp nht tht bi");
                console.log(error);
                return;
            }
        }
        CallApi()

    }
    useEffect(() => {
        const CallApi = async () => {
            const { data } = await NewApi.get(id);
            setNews(data)
            reset(data)
        }
        CallApi(id)
    }, [id]);

    return (
        <div className="mt-28 container-fluid">
            <div className="row">
                <main>
                    <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                        <h1 className="h2">Cp nht tin tc</h1>
                    </div>
                    <div className="table-responsive" >
                        <form className="w-75" onSubmit={handleSubmit(onSubmit)}>
                            <div className="mb-3">
                                <label htmlFor="exampleInputEmail1" className="form-label">Tiu </label>
                                <input
                                    type="text"
                                    className="form-control"
                                    {...register('title', { required: true })}
                                    defaultValue={news.title}
                                />
                                {errors.title && <span className="text-danger">Hy chn tiu </span>}

                            </div>
                            <div className="mb-3">
                                <label htmlFor="exampleInputEmail1" className="form-label">Link nh ba</label>
                                <input
                                    type="text"
                                    className="form-control"
                                    {...register('image', { required: true })}
                                    defaultValue={news.image}

                                />
                                {errors.image && <span className="text-danger">Hy chn nh bi vit</span>}
                            </div>
                            <div className="mb-3">
                                <label htmlFor="exampleInputEmail1" className="form-label">Ni dung</label>
                                <input
                                    type="text"
                                    className="form-control"
                                    {...register('content', { required: true })}
                                    defaultValue={news.content}

                                />
                                {errors.content && <span className="text-danger">Hy nhp ni dung</span>}

                            </div>

                            <button type="submit" className="btn btn-primary">Submit</button>
                        </form>
                    </div>
                </main>
            </div>
        </div>
    )
}

export default UpdateNew
